<template>
  <div class="slider">
    <div class="slider-content" :style="{ 'margin-left': x * -34.5 + 'vh' }">
      <template v-for="(section, id) in slider">
        <div class="section" :key="id">
          <img class="section-image" :src="getSectionImage(section)" />
          <div>
            <span class="title">{{ section.title }}</span>
            <span class="sub-title">{{ section.subTitle }}</span>
          </div>
          <div class="section-id">
            {{ section.id }}
          </div>
          <div class="section-arrow">
            <a :href="section.url" target="_blank">
              <img
                v-if="$store.state.theme === 'light'"
                class="section-arrow-image"
                src="@/assets/images/default_enter.svg"
              />
              <img
                v-if="$store.state.theme === 'light'"
                class="section-enter-image"
                src="@/assets/images/hover_enter.svg"
              />
              <img
                v-if="$store.state.theme === 'dark'"
                class="section-arrow-image"
                src="@/assets/images/dark/default_enter.svg"
              />
              <img
                v-if="$store.state.theme === 'dark'"
                class="section-enter-image"
                src="@/assets/images/dark/hover_enter.svg"
              />
            </a>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Slider',
  props: {
    x: Number
  },
  components: {},
  watch: {
    x: function (newVal, oldVal) {
      //右側已可見，不移動
      if (
        newVal > oldVal &&
        oldVal >= 0 &&
        this.checkSliderRightEdgeVisible()
      ) {
        this.$emit('update-slide-x', oldVal)
      }

      let newX = newVal

      if (newX > this.slider.length - 1) {
        newX = this.slider.length - 1
      }

      if (newX < 0) {
        newX = 0
      }

      if (newX !== newVal) {
        this.$emit('update-slide-x', newX)
      }
    }
  },
  data: function () {
    return {
      slider: []
    }
  },
  mounted() {
    this.fetchSlider()
  },
  methods: {
    fetchSlider: function () {
      const that = this

      fetch('/api/slider.json', {})
        .then(function (response) {
          if (response.ok) {
            return response.json()
          }

          throw new Error(response.statusText)
        })
        .then(function (res) {
          that.slider.splice(0, that.slider.length)

          for (let i in res) {
            that.slider.push(res[i])
          }
        })
        .catch(function (e) {
          console.log('Fetch failed', e)
        })
    },
    checkSliderRightEdgeVisible: function () {
      let rect = this.$refs['slider-right-edge'].getBoundingClientRect()

      return rect.left + rect.width < window.innerWidth
    },
    getSectionImage(section) {
      switch (this.$store.state.theme) {
        case 'dark':
          return section['image-dark']
        default:
          return section.image
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/theme.scss';

.slider {
  height: 100%;
  display: flex;
  width: 100%;
  overflow: hidden;

  .slider-content {
    display: flex;
    transition-duration: 0.6s;
    width: 100%;
  }
}
.section {
  overflow: hidden;
  width: 100%;
  position: relative;
  height: 100%;
  text-align: center;

  .section-image {
    width: 64vh;
    transition-duration: 0.3s;
  }

  .title {
    font-size: 5vh;
    font-weight: bold;
    padding: 0 1vh;
  }

  .sub-title {
    font-size: 3vh;
    font-weight: bold;
    padding: 0 1vh;
    margin-right: 6vh;
  }

  .section-id {
    font-size: 14vh;
    line-height: 14vh;
    font-family: 'AnticDidone';
    position: absolute;
    bottom: 0;
  }

  .section-arrow {
    .section-arrow-image {
      opacity: 1;
      position: absolute;
      bottom: 0;
      right: 2vh;
      height: 2vw;
      transition-duration: 0.3s;
    }

    .section-enter-image {
      opacity: 0;
      position: absolute;
      bottom: 0;
      right: 2vh;
      height: 4vh;
      transition-duration: 0.3s;
    }
  }

  &:hover {
    .section-arrow-image {
      opacity: 0;
    }

    .section-enter-image {
      opacity: 1;
      height: 8vh;
    }
  }
}
</style>
