<template>
  <div class="home d-flex">
    <div class="title-wrapper">
      <img
        v-if="$store.state.theme === 'light'"
        class="title-img"
        src="@/assets/images/title.svg"
      />
      <img
        v-if="$store.state.theme === 'dark'"
        class="title-img"
        src="@/assets/images/dark/title.svg"
      />
      <div class="title-text text-end">開源智造醫療AI平台</div>
    </div>
    <div class="slider-wrapper">
      <slider
        :x="sliderX"
        @update-slide-x="updateSlideX"
        @prev="move(-1)"
        @next="move(1)"
      ></slider>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Slider from '@/components/Slider.vue'

export default {
  name: 'Home',
  data: function () {
    return {
      sliderX: 0
    }
  },
  components: {
    Slider
  },
  methods: {
    move: function (dir) {
      this.sliderX += dir
    },
    updateSlideX: function (newX) {
      this.sliderX = newX
    }
  }
}
</script>

